.notificationBarContainer {
    margin-top: 70px;
}
.notificationBar {
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 40px;
    user-select: none;
    z-index: 1001;
    width: 100%;
    background: #faa52d;
    transition: 0.3s;
}
.notificationBar__text,
.notificationBar__textMobile {
    font-size: 15px;
}
.notificationBar__textMobile,
.notificationBar__buttonMobile {
    display: none;
}

.notificationBar__button {
    height: 35px;
    background: var(--primary-color);
    font-weight: 500;
    font-size: 13px;
    text-transform: uppercase;
    color: #ffffff;
    cursor: pointer;
    border-radius: 3px;
    padding: 0 10px;
}

/* Responsive */
@media only screen and (max-width: 420px) {
    .notificationBar {
        background: green;
    }
    .notificationBar__text,
    .notificationBar__button {
        display: none;
    }
    .notificationBar__textMobile {
        display: block;
        font-size: 12px;
        color: #fff;
    }
    .notificationBar__buttonMobile {
        display: flex;
        align-items: center;
        stroke: #fff;
    }
}
